import React, { Component } from 'react';
import s from './Post.module.css';

class Post extends Component<TypeProps> {
    render() {
        return (
            <div className={s.posts}>
                <div className={s.item}>
                    {this.props.message}
                    <img
                        src="data:image/jpeg;base64,"
                        alt=""/>
                    <div>
                        <span>like: {this.props.like}</span>
                    </div>
                </div>
            </div>
        )
    }
}

type TypeProps = {
    message: string,
    like: number
}

export default Post;
